import React from 'react';
import * as Sentry from '@sentry/react';

const SentryTest: React.FC = () => {
  const triggerError = () => {
    // This will trigger a JavaScript error that Sentry will capture
    throw new Error('Test error for Sentry - This is intentional!');
  };

  const triggerManualCapture = () => {
    // Manually capture an event
    Sentry.captureMessage('Manual test message from Sentry integration', 'info');
    alert('Manual event sent to Sentry (check console in dev mode)');
  };

  const triggerUserFeedback = () => {
    // In development, events aren't sent to Sentry so there's no event ID
    // Use NODE_ENV instead of DEV since it's more universally available
    if (process.env.NODE_ENV === 'development') {
      alert(
        'User Feedback Dialog only works in production mode where events are actually sent to Sentry. In development, events are only logged to console.'
      );
      return;
    }

    // This will work in production after an actual error is captured
    Sentry.showReportDialog({
      user: {
        name: 'Test User',
        email: 'test@example.com',
      },
    });
  };

  return (
    <div style={{ padding: '20px', border: '2px dashed #ccc', margin: '20px' }}>
      <h3>üõ†Ô∏è Sentry Testing Panel</h3>
      <p>Use these buttons to test your Sentry integration:</p>

      <div style={{ display: 'flex', gap: '10px', flexWrap: 'wrap' }}>
        <button
          onClick={triggerError}
          style={{
            padding: '10px 15px',
            backgroundColor: '#ff4757',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer',
          }}
        >
          üö® Trigger Error
        </button>

        <button
          onClick={triggerManualCapture}
          style={{
            padding: '10px 15px',
            backgroundColor: '#3742fa',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer',
          }}
        >
          üì§ Send Manual Event
        </button>

        <button
          onClick={triggerUserFeedback}
          style={{
            padding: '10px 15px',
            backgroundColor: '#2ed573',
            color: 'white',
            border: 'none',
            borderRadius: '4px',
            cursor: 'pointer',
          }}
        >
          üí¨ User Feedback Dialog
        </button>
      </div>

      <div
        style={{
          marginTop: '15px',
          padding: '10px',
          backgroundColor: '#f8f9fa',
          borderRadius: '4px',
        }}
      >
        <small>
          <strong>Development Mode:</strong> Events are logged to console but not sent to Sentry.
          <br />
          <strong>Production Mode:</strong> Events sent to Sentry dashboard with session replay.
          <br />
          <strong>User Feedback:</strong> Only works in production with actual error events.
        </small>
      </div>
    </div>
  );
};

export default SentryTest;
